<template>
  <div class="province-picker">
    <div class="mask" @click="onClose"></div>
    <div class="content">
      <div class="title">{{title}}<mt-button @click.native="onClose" class="btn-close"><img src="../../../../assets/mine/ic_close@2x.png" height="22" width="22" slot="icon"></mt-button></div>
      <div class="radio-wrap">
        <template v-if="options.length > 1">
          <label v-for="item in options" v-bind:key="item.area_id" v-bind:class="[item.area_id == currentId ? 'checked' : '', 'ui-radio']">
            <input v-bind:id="item.area_id" type="radio" name="radio" v-model="value" v-bind:value="item.area_name" @change="handle($event)">{{item.area_name}}
          </label>
        </template>
        <template v-else-if="options.length <= 1">
          <label v-for="item in options" v-bind:key="item.area_id" v-bind:class="[item.area_id == currentId ? 'checked' : '', 'ui-radio']">
            <input v-bind:id="item.area_id" type="radio" name="radio" v-model="value" v-bind:value="item.area_name" @click="handle($event)">{{item.area_name}}
          </label>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { Button } from 'mint-ui'

export default {
  name: 'provincePicker',
  props: {
    options: { type: Array },
    title: { type: String },
    addressType: { type: Number },
    currentValue: { type: String },
    currentId: { type: String }
  },
  data: function () {
    return {
      value: ''
    }
  },
  methods: {
    onClose () {
      this.$emit('close')
    },
    handle (event) {
      var el = event.currentTarget.parentNode
      el.className = 'ui-radio checked'
      this.currentId = event.currentTarget.id
      this.$emit('setValue', { addressType: this.addressType, name: this.value, id: this.currentId })
      this.$emit('close')
    }
  },
  components: {
    [Button.name]: Button
  }
}
</script>

<style scoped lang="scss">
  div.province-picker {
    position: absolute;
    z-index: 2035;
    width: 100%;
    height: 100%;
    max-width: 640px;
    top: 0;
    left: 0;
    > div.mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0,0,0,0.5);
      z-index: 2;
    }
    > div.content {
      position: absolute;
      bottom: 0;
      z-index: 3;
      width: 100%;
      padding: 0 25px;
      height: 45%;
      background-color: $background-light-color;
      overflow-y: scroll;
      text-align: left;
      > div.title {
        @if $IF365 { color: #CCC; }
        @else { color: #999; }
        font-size: 15px;
        height: 45px;
        line-height: 45px;
        position: fixed;
        left: 0;
        width: 100%;
        padding: 0 25px;
        z-index: 6;
        background-color: $background-light-color;
        .btn-close {
          background: none;
          border: none;
          box-shadow: none;
          float: right;
          height: 30px;
          width: 30px;
          text-align: right;
          padding: 0;
          margin-top: 10px;
        }
      }
      > div.radio-wrap {
        margin-top: 40px;
        position: relative;
        z-index: 5;
        label.ui-radio {
          display: block;
          border-bottom: 1px solid $border-color;
          position: relative;
          height: 49px;
          line-height: 49px;
          font-size: 16px;
          color: $font-color;
          input[type="radio"] {
            opacity: 0;
            position: absolute;
            right: 0;
          }
        }
        label.ui-radio.checked {
          @if $IF365 { background-image: url("../../../../assets/mine/ic_address_pic_365@2x.png"); }
          @else { background-image: url("../../../../assets/mine/ic_address_pic@2x.png"); }
          color: $back-color;
          background-repeat: no-repeat;
          background-position: right center;
          background-size: auto 50%;
        }
      }
    }
  }
</style>
