<template>
  <div>
    <div @click="handleExample(0)">
      <input type="text" disabled placeholder="请选择省份" v-model="province_name" >
    </div>
    <div @click="handleExample(1)">
      <input type="text" disabled placeholder="请选择城市" v-model="city_name" >
    </div>
    <div @click="handleExample(2)">
      <input type="text" disabled placeholder="请选择区/县" v-model="county_name" >
    </div>
    <province-picker v-bind:currentValue="this.currentValue" v-bind:currentId="this.currentId" v-bind:title="this.title" v-bind:options="this.options" v-bind:addressType="this.addressType" v-show="this.show" @close="onCloseSelectProvince" @setValue="setRadioValue"></province-picker>
  </div>
</template>
<script>
import ProvincePicker from './ProvincePicker.vue'
import http from '@/tool/http'
import { Toast } from 'mint-ui'

export default {
  name: 'selectProvince',
  props: {
    list: { type: Object }
  },
  data: function () {
    return {
      currentValue: '',
      currentId: '',
      show: false,
      addressType: 0,
      titleList: ['选择省份', '选择城市', '选择区/县'],
      title: '',
      options: [],
      provinceOptions: [],
      cityOptions: [],
      countyOptions: [],
      province_name: this.list.province_name,
      city_name: this.list.city_name,
      county_name: this.list.county_name,
      provinceId: this.list.province,
      cityId: this.list.city,
      countyId: this.list.county
    }
  },
  methods: {
    onCloseSelectProvince () {
      this.show = false
    },
    handleExample (val) {
      if (val === 0) {
        this.addressType = val
        this.title = this.titleList[val]
        if (this.provinceOptions.length === 0) {
          http.getProvince().then(data => {
            if (data) {
              this.provinceOptions = data
              this.options = this.provinceOptions
            } else {
              Toast('获取省份失败')
            }
          })
        } else {
          this.options = this.provinceOptions
        }
        this.show = true
        this.currentValue = this.province_name
        this.currentId = this.provinceId
      } else {
        if (val === 1 && this.province_name && this.provinceId) {
          this.addressType = val
          this.title = this.titleList[val]
          this.options = []
          http.getCity(this.provinceId).then(data => {
            if (data) {
              this.cityOptions = data
              this.options = this.cityOptions
            } else {
              Toast('获取城市失败')
            }
          })
          this.show = true
          this.currentValue = this.city_name
          this.currentId = this.cityId
        } else if (val === 2 && this.province_name && this.city_name && this.provinceId && this.cityId) {
          this.addressType = val
          this.title = this.titleList[val]
          this.options = []
          http.getCounty(this.cityId).then(data => {
            if (data) {
              this.countyOptions = data
              this.options = this.countyOptions
            } else {
              Toast('获取区/县失败')
            }
          })
          this.show = true
          this.currentValue = this.county_name
          this.currentId = this.countyId
        }
      }
    },
    setRadioValue (obj) {
      if (obj.addressType === 0) {
        this.province_name = obj.name
        this.city_name = ''
        this.county_name = ''

        this.provinceId = obj.id
        this.cityId = ''
        this.countyId = ''
      } else if (obj.addressType === 1) {
        this.city_name = obj.name
        this.county_name = ''

        this.cityId = obj.id
        this.countyId = ''
      } else if (obj.addressType === 2) {
        this.county_name = obj.name
        this.countyId = obj.id
      }
      this.$emit('setProvince', { province_name: this.province_name, city_name: this.city_name, county_name: this.county_name, province: this.provinceId, city: this.cityId, county: this.countyId })
    }
  },
  components: {
    ProvincePicker
  }
}
</script>
<style scoped lang="scss">
  input {
    color: $font-color;
    border: none;
    outline: none;
    width: 100%;
    font-size: 16px;
    height: 56px;
    line-height: 56px;
    margin-bottom: 10px;
    background: none;
    border-bottom: 1px solid $border-color;
  }
</style>
